    	},
			props:['text'],
    	created(){
    	},
    	computed: {
    	},
			methods: {
				async handleCommand(command) {
					if(command == 'signout'){
							http.post(api.logout).then((res)=>{
								if(res.status === 10000){
									sessionStorage.setItem("token", null);
									window.location.href = 'http://47.93.8.25:8083/admin/index.html';
								}else{
									this.$message({
				            type: 'error',
				            message: res.msg
				        });
								}
							})
					}
				},
			}
    }
</script>

