<template>
  	<div class="login_page fillcontain">
	  	<transition name="form-fade" mode="in-out">
	  		<section class="form_contianer" v-show="showLogin">
		  		<div class="manage_tip">
		  			<p>管理系统登录</p>
		  		</div>
		    	<el-form :model="loginForm" :rules="rules" ref="loginForm">
						<p class="welcom">欢迎来到慕慕生鲜后台管理系统</p>
						<p class="welcom-tip">用户名imooc 密码12345678</p>
					<el-form-item prop="username">
						<div class="login-item" >

						<span>用户名</span>
						<el-input v-model="loginForm.username" placeholder="请输入用户名"></el-input>
						</div>
					</el-form-item>
					<el-form-item   prop="password">
						<div class="login-item" >
						<span>密码</span>
						<el-input type="password" placeholder="请输入密码" v-model="loginForm.password"></el-input>
						</div>
					</el-form-item>
					<el-form-item>
				    	<el-button type="primary" @click="submitForm('loginForm')" class="submit_btn">登陆</el-button>
				  	</el-form-item>
				</el-form>
	  		</section>
	  	</transition>
  	</div>
</template>

<script>
/* eslint-disable */
	import http from './http';
import api from './_api';

	export default {
	    data(){
			return {
				loginForm: {
					username: '',
					password: '',
				},
				rules: {
					username: [
			            { required: true, message: '请输入用户名', trigger: 'blur' },
			        ],
					password: [
						{ required: true, message: '请输入密码', trigger: 'blur' }
					],
				},
				showLogin: false,
			}
		},
		mounted(){
			this.showLogin = true;
		},
		computed: {
		},
		methods: {
			async submitForm(formName) {
				this.$refs[formName].validate(async (valid) => {
					if (valid) {
						// const res = await login({user_name: this.loginForm.username, password: this.loginForm.password})
						
						http.get(api.login,{userName:this.loginForm.username,password:this.loginForm.password}).then((res)=>{
								if(res.status === 10000){
										console.log('user',res.data.username);
										localStorage.setItem('username',res.data.username);
										localStorage.setItem('personalizedSignature',res.data.personalizedSignature);
										localStorage.setItem('role',res.data.role);
												this.$router.push('good')
								}else{
									this.$message({
											type: 'error',
											message: res.msg
									});
								}
        })
					} else {
						this.$notify.error({
							title: '错误',
							message: '请输入正确的用户名密码',
							offset: 100
						});
						return false;
					}
				});
			},
		}
	}
</script>

